//* Imported Constants
import {
  //* CREATE ORDER CONSTANTS
  ORDER_CREATE_REQUEST,
  ORDER_CREATE_SUCCESS,
  ORDER_CREATE_FAIL,
  //* CREATE ORDER DETAILS CONSTANTS
  ORDER_DETAILS_FAIL,
  ORDER_DETAILS_REQUEST,
  ORDER_DETAILS_SUCCESS,
  //* CREATE PAY ORDER CONSTANTS
  ORDER_PAY_FAIL,
  ORDER_PAY_REQUEST,
  ORDER_PAY_RESET,
  ORDER_PAY_SUCCESS,
  //* CREATE MY ORDER LIST CONSTANTS
  ORDER_LIST_MY_FAIL,
  ORDER_LIST_MY_REQUEST,
  ORDER_LIST_MY_SUCCESS,
  ORDER_LIST_MY_RESET,
  //* ORDER LIST CONSTANTS
  ORDER_LIST_FAIL,
  ORDER_LIST_REQUEST,
  ORDER_LIST_SUCCESS,
  //* ORDER DELIVER CONSTANTS
  ORDER_DELIVER_FAIL,
  ORDER_DELIVER_REQUEST,
  ORDER_DELIVER_SUCCESS,
  ORDER_DELIVER_RESET,
} from '../constants/orderConstants'

//? ================ 1)  Create Order Reducer ===========
export const orderCreateReducer = (state = {}, action) => {
  switch (action.type) {
    //* Create Order Request Case
    case ORDER_CREATE_REQUEST:
      return {
        loading: true,
      }
    //* Create Order Success Case
    case ORDER_CREATE_SUCCESS:
      return {
        loading: false,
        success: true,
        order: action.payload,
      }
    //* Create Order Fail Case
    case ORDER_CREATE_FAIL:
      return {
        loading: false,
        error: action.payload,
      }
    default:
      return state
  }
}

//? ================ 2) Order Details Reducer ===========
export const orderDetailsReducer = (
  state = { loading: true, orderItems: [], shippingAddress: {} },
  action
) => {
  switch (action.type) {
    //*  Order Details Request Case
    case ORDER_DETAILS_REQUEST:
      return {
        ...state,
        loading: true,
      }
    //*  Order Details Success Case
    case ORDER_DETAILS_SUCCESS:
      return {
        loading: false,
        order: action.payload,
      }
    //*  Order Details Fail Case
    case ORDER_DETAILS_FAIL:
      return {
        loading: false,
        error: action.payload,
      }
    default:
      return state
  }
}

//? ================ 3) Order Pay Reducer ===========
export const orderPayReducer = (state = {}, action) => {
  switch (action.type) {
    //*  Order PAY Request Case
    case ORDER_PAY_REQUEST:
      return {
        loading: true,
      }
    //*  Order PAY Success Case
    case ORDER_PAY_SUCCESS:
      return {
        loading: false,
        success: true,
      }
    //*  Order PAY Fail Case
    case ORDER_PAY_FAIL:
      return {
        loading: false,
        error: action.payload,
      }
    //*  Order PAY Fail Case
    case ORDER_PAY_RESET:
      return {}
    default:
      return state
  }
}

//? ================ 4) Order List My Reducer ===========
export const orderListMyReducer = (state = { orders: [] }, action) => {
  switch (action.type) {
    //*  My List Order Request Case
    case ORDER_LIST_MY_REQUEST:
      return {
        loading: true,
      }
    //*  My List Order Success Case
    case ORDER_LIST_MY_SUCCESS:
      return {
        loading: false,
        orders: action.payload,
      }
    //*  My List Order Fail Case
    case ORDER_LIST_MY_FAIL:
      return {
        loading: false,
        error: action.payload,
      }
    //*  My List Order Fail Case
    case ORDER_LIST_MY_RESET:
      return { orders: [] }
    default:
      return state
  }
}

