//? ================ 6) Order Deliver Reducer ===========
export const orderDeliverReducer = (state = {}, action) => {
  switch (action.type) {
    //*  Order Deliver Request Case
    case ORDER_DELIVER_REQUEST:
      return {
        loading: true,
      }
    //*  Order Deliver Success Case
    case ORDER_DELIVER_SUCCESS:
      return {
        loading: false,
        success: true,
      }
    //*  Order Deliver Fail Case
    case ORDER_DELIVER_FAIL:
      return {
        loading: false,
        error: action.payload,
      }
    //*  Order Deliver Reset Case
    case ORDER_DELIVER_RESET:
      return {}
    default:
      return state
  }
}